
// bibliotek
import React from "react";
import Marquee from "react-fast-marquee";
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
// import {useRef, useEffect} from "react";
// componets
import Paragraph from "../paragraph/Paragraph";

// style
import "./grid_animation.scss";

// images
import komand_1 from "../../assets/komand/кильян/1.jpg";
import komand_2 from "../../assets/komand/кильян/2.jpg";
import komand_3 from "../../assets/komand/кильян/3.jpg";
import komand_4 from "../../assets/komand/кильян/4.jpg";

import komand_5 from "../../assets/komand/галина/1.jpg";
import komand_6 from "../../assets/komand/галина/2.jpg";
import komand_7 from "../../assets/komand/галина/3.jpg";
import komand_8 from "../../assets/komand/галина/4.jpg";
import komand_9 from "../../assets/komand/галина/5.jpg";





gsap.registerPlugin(ScrollTrigger);

function GridAnimation(){
    return(
        <div className="komanda__wrapper" id="team">
            <Paragraph title="Our team"/>
            <div className="komanda__container _galina">
                <div className="komanda__text" >
                    <Marquee speed={120}>
                        <div className="komanda__text__container">
                            <h4>ceo</h4> 
                            <span>ceo</span>
                            <img src={komand_7} alt="" />
                            <img src={komand_9} alt="" />
                            <h4>ceo</h4> 
                            <span>ceo</span> 
                            <img src={komand_9} alt="" />
                            <img src={komand_8} alt="" />
                        </div>
                    </Marquee>
                </div>
                <div className="komanda__text" >
                    <Marquee speed={160} direction="right">
                        <div className="komanda__text__container">
                            <h4>Galina</h4> 
                            <img src={komand_9} alt="" />
                            <img src={komand_5} alt="" />
                            <span>Lavrishcheva</span> 
                            <img src={komand_7} alt="" />
                            <img src={komand_6} alt="" />
                            <h4>Lavrishcheva</h4>
                            <img src={komand_9} alt="" />
                            <img src={komand_6} alt="" /> 
                            <span>Lavrishcheva</span> 
                        </div>
                    </Marquee>
                </div>
            </div>
            <div className="komanda__container _kilian">
            <div className="komanda__text" >
                    <Marquee>
                        <div className="komanda__text__container">
                            <h4>ART</h4> 
                            <span>DIRECTOR</span>
                            <img src={komand_1} alt="" />
                            <img src={komand_4} alt="" />
                            <h4>ART</h4> 
                            <span>DIRECTOR</span> 
                            <img src={komand_3} alt="" />
                            <img src={komand_4} alt="" />
                        </div>
                    </Marquee>
                </div>
                <div className="komanda__text" >
                    <Marquee speed={130} direction="right">
                        <div className="komanda__text__container">
                            <h4>kilian</h4> 
                            <span>Svetlana</span>
                            <img src={komand_4} alt="" />
                            <img src={komand_1} alt="" />
                            <h4>Svetlana</h4> 
                            <span>kilian</span> 
                            <img src={komand_2} alt="" />
                            <img src={komand_4} alt="" />
                        </div>
                    </Marquee>
                </div>
            </div>
        </div>
    )
}

export default GridAnimation;